import myArticles from './myArticles.vue';
import myVote from './myVote.vue';

var loadArticleFlag = false;
export default {
	components: {
		myArticles,
		myVote,
	},
	data() {
		return {
			userInfo: '',
			binNum: '12',

			totalPage: 1,
			currentPage: 1,
			totalNum: '0',
			myArticleList: '',
			
			myVoteList:'',
			swiperViewing: "article",
		};
	},

	onLoad() {
		var userInfo = this.getGlobalUserInfo();
		console.log(userInfo);
		if (this.isNull(userInfo)) {
			uni.redirectTo({
				url: '../signin/signin'
			});
			return;
		} else {
			this.userInfo = userInfo; // 刷去默认值(若有)
		}

		this.mySocket.init(); // 初始化 Socket, 离线调试请注释掉
		var page = this.currentPage;
		this.showArticles(page);
		this.showVotes();

		uni.$on("refresh", () => {
			this.showArticles(1);
		});
	},

	methods: {
		// 锁
		showArticles: function(page) {
			if (loadArticleFlag) {
				loadArticleFlag = false;
			}

			loadArticleFlag = true;

			uni.showLoading({
				title: '加载中...'
			});
			setTimeout(() => {
				if (loadArticleFlag) {
					loadArticleFlag = false; //解锁
					uni.hideLoading();
					uni.showToast({
						title: '网络未知错误',
						icon: 'none',
						duration: 1000
					});
				}
			}, 5000); //延时五秒timeout

			var that = this;
			uni.request({
				url: that.$serverUrl + '/article/queryPublishHistory',
				method: 'POST',
				data: {
					page: page,
					userId: that.userInfo.id,
					targetId: that.userInfo.id //应该为targetId,但缺少publicProfile的数据传输,暂时用userId测试
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					setTimeout(() => {
						//延时加载
						uni.hideLoading();
						loadArticleFlag = false;

						console.log(res);
						if (page == 1) {
							that.myArticleList = [];
						}
						var newArticleList = res.data.data.rows;
						var oldArticleList = that.myArticleList;
						that.myArticleList = oldArticleList.concat(newArticleList);
						that.currentPage = page;
						that.totalPage = res.data.data.total;
						that.totalNum = res.data.data.records;
						console.log(that.totalNum);
					}, 300);
				},
				fail: res => {
					uni.hideLoading();
					loadArticleFlag = false;
					console.log('index unirequest fail');
				}
			});
		},
		loadMore: function() {
			var that = this;
			var currentPage = that.currentPage;
			var totalPage = that.totalPage;
			// 判断当前页数和总页数是否相等
			if (currentPage == totalPage) {
				// that.showArticles(1);
				uni.showToast({
					title: '没有更多文章了',
					icon: 'none',
					duration: 1000
				});
			} else {
				var page = currentPage + 1;
				that.showArticles(page);
			}
		},
		switchSwiper(a){
			this.swiperViewing = a ;
		},
		showVotes: function(page) {	
			var that = this;
			uni.request({
				url: that.$serverUrl + '/vote/queryAllVotes',
				method: 'POST',
				data: {
					page: 1,
					userId: that.userInfo.id,
					pagesize:'10',
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
					that.myVoteList = res.data.data.rows;

				},
				fail: res => {
					uni.hideLoading();
					loadArticleFlag = false;
		
					console.log('index unirequest fail');
					console.log(res);
				}
			});
		},
	}
};
